export default{
	props:{
		goodsInfo:Object
	},
	methods:{
		//减少数量
		sub(){
			if(this.goodsInfo.number<=1){
				return;
			}
			this.goodsInfo.number--;
		    this.$emit('change')
		},
		//增加数量
		add(){
			this.goodsInfo.number++;
			this.$emit('change')
		}
	}
}
